import React, { useState, useEffect, Fragment } from 'react';
import { GetTopWrapper } from '../style';
/* 独立可复用的回到顶部组件 */

const GetTop = () => {

	const [scroll, setScroll] = useState(false);

	useEffect(()=>{
		//回到顶部
		window.addEventListener("scroll", changeScrollTopShow);
		//移除
		return () => {
			window.removeEventListener("scroll", changeScrollTopShow);
		}
	})

	function changeScrollTopShow() {
		if(document.documentElement.scrollTop > 100) {
			setScroll(true);
		} else {
			setScroll(false);
		}
	}

	function getToTop() {
		window.scrollTo(0,0);
	}

	//条件渲染
	if(scroll) {
		return (
			<GetTopWrapper onClick={ getToTop }><span className="iconfont">&#xe600;</span></GetTopWrapper>
		)
	} else return (
		<Fragment> </Fragment>
	)
}

export default GetTop;